
.navigation {
  width: 100%;
  border-bottom: 1px solid #ccd7d7;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 16px;
}

.header-menu {
  display: flex;
  justify-content: center;
  max-width: 1150px;
  margin: 0 auto;
  padding-left: 0;
}
.menu__item {
  display: inline-block;
  white-space: nowrap;
}
@media screen and (max-width: 480px) {
  .menu__item:nth-child(n + 3) {
    display: none;
  }
  .menu__item:nth-child(2) .menu__link {
    border-right: 0;
  }
}
@media screen and (max-width: 768px) {
  .menu__item:nth-child(n + 4) {
    display: none;
  }
  .menu__item:nth-child(3) .menu__link {
    border-right: 0;
  }
}
@media screen and (max-width: 992px) {
  .menu__item:nth-child(n + 5) {
    display: none;
  }
  .menu__item:nth-child(4) .menu__link {
    border-right: 0;
  }
}
.menu__item:last-child .menu__link {
  border-right: 0;
}
.menu__link {
  display: block;
  padding: 6px 30px 3px;
}
.menu__link:hover .menu__first-word, .menu__link:focus .menu__first-word {
  transform: translate3d(0, -105%, 0);
}
.menu__link:hover .menu__second-word, .menu__link:focus .menu__second-word {
  transform: translate3d(0, 105%, 0);
}
@media screen and (min-width: 768px) {
  .menu__link {
    padding: 6px 40px 3px;
  }
}
.menu__title {
  display: inline-block;
  overflow: hidden;
}
.menu__first-word, .menu__second-word {
  display: inline-block;
  position: relative;
  transition: transform 0.3s;
}
.menu__first-word::before, .menu__second-word::before {
  position: absolute;
  content: attr(data-hover);
}
.menu__first-word {
  color: #FFFFFF;
}
.menu__first-word::before {
  top: 105%;
  color: #7395AE;
}
.menu__second-word {
  color: #7395AE;
}
.menu__second-word::before {
  bottom: 105%;
  color: #FFFFFF;
}

